@charset "UTF-8";
/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
* {
  box-sizing: border-box;
}

*:focus {
  outline: none;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
}

body {
  line-height: 1;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

ul,
dl,
ol {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: normal;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none;
  color: #000;
}

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none;
}

img {
  border-style: none;
  height: auto;
  max-width: 100%;
  vertical-align: bottom;
}

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold;
}

del {
  text-decoration: line-through;
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0;
}

input,
select {
  vertical-align: middle;
}

button,
input,
optgroup,
select,
textarea {
  font: inherit;
  margin: 0;
  padding: 0;
  border: 0;
  border-radius: 0;
  background-color: transparent;
}

textarea {
  display: block;
  overflow-y: auto;
  resize: vertical;
}

[type=submit],
[type=button],
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

[hidden] {
  display: none;
}

[disabled] {
  cursor: not-allowed;
}

:focus:not(.focus-visible) {
  outline: 0;
}

html,
body {
  height: 100vh;
}

html {
  font-size: 62.5%;
}

body {
  position: relative;
  overflow-x: hidden;
  font-family: "Noto Sans JP", "游ゴシック体", YuGothic, "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "Hiragino Kaku Gothic ProN", "Hiragino Kaku Gothic Pro", "メイリオ", "Meiryo", sans-serif;
  color: #152e30;
  font-size: 16px; /*IE9以下で反映されるよう記述*/
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.6;
}

body::before {
  display: block;
  padding: 80px 0 0;
  content: "";
}
@media screen and (max-width: 1024px) {
  body::before {
    padding: 80px 0 0;
  }
}
@media screen and (max-width: 767px) {
  body::before {
    padding: 56px 0 0;
  }
}

::placeholder {
  color: #ccc;
}

/* スライダーちらつき防止 */
strong,
b,
em {
  font-weight: 700;
  font-style: normal;
}

.u-hidden {
  display: none;
}

@media screen and (min-width: 768px) {
  .u-hidden-pc {
    display: none !important;
  }
}

@media screen and (max-width: 1024px) {
  .u-hidden-tab {
    display: none !important;
  }
}

@media screen and (max-width: 767px) {
  .u-hidden-sp {
    display: none !important;
  }
}

/*----------------------------------------
	Media
----------------------------------------*/
@media screen and (max-width: 1024px) {
  .is-pc,
  .pc,
  .u-visible-pc {
    display: none !important;
  }
}
@media screen and (max-width: 767px) {
  .is-pc,
  .pc,
  .u-visible-pc {
    display: none !important;
  }
}

.is-tab,
.tablet,
.u-visible-tab {
  display: none !important;
}
@media screen and (max-width: 1024px) {
  .is-tab,
  .tablet,
  .u-visible-tab {
    display: block !important;
  }
}

.is-sp,
.sp,
.u-visible-sp {
  display: none !important;
}
@media screen and (max-width: 767px) {
  .is-sp,
  .sp,
  .u-visible-sp {
    display: block !important;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/*------------------------------------------------------------------------
	etc
------------------------------------------------------------------------*/
@keyframes nav-link-enter {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0%);
  }
}
@keyframes nav-link-leave {
  0% {
    transform: translateX(0%);
  }
  100% {
    transform: translateX(130%);
  }
}
@keyframes c-btn-enter {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes c-btn-leave {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(100%);
  }
}
@keyframes c-btn-arrow {
  0% {
    -webkit-transform: translateY(-50%) translateX(0);
    transform: translateY(-50%) translateX(0);
    opacity: 1;
  }
  25% {
    -webkit-transform: translateY(-50%) translateX(3px);
    transform: translateY(-50%) translateX(3px);
    opacity: 0;
  }
  50% {
    -webkit-transform: translateY(-50%) translateX(6px);
    transform: translateY(-50%) translateX(6px);
    opacity: 0;
  }
  50.1% {
    -webkit-transform: translateY(-50%) translateX(-5px);
    transform: translateY(-50%) translateX(-5px);
    opacity: 0;
  }
  90% {
    -webkit-transform: translateY(-50%) translateX(0);
    transform: translateY(-50%) translateX(0);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateY(-50%) translateX(0);
    transform: translateY(-50%) translateX(0);
    opacity: 1;
  }
}
body[data-status=loading] .l-loader-lower {
  pointer-events: auto;
  opacity: 1;
}

body[data-status=loadstart] .l-loader-lower {
  pointer-events: auto;
  opacity: 1;
}

.l-loader-lower {
  position: fixed;
  z-index: 99999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  pointer-events: none;
  opacity: 0;
  transition: opacity cubic-bezier(0.36, 0.14, 0, 1) 1s;
}
.l-loader-lower__bg {
  position: absolute;
  z-index: 99999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: #fff;
}

