  .l-col2 {
    width: 100%;
    padding: 0 24px;
    display: block;
  }
}
.l-col2__main {
  width: 70.75%;
  max-width: 816px;
}
@media screen and (max-width: 767px) {
  .l-col2__main {
    max-width: 100%;
    width: 100%;
  }
}
.l-col2__aside {
  width: 29.25%;
  min-width: 200px;
  max-width: 320px;
  margin-left: 64px;
}
@media screen and (max-width: 1024px) {
  .l-col2__aside {
    margin-left: 40px;
  }
}
@media screen and (max-width: 767px) {
  .l-col2__aside {
    max-width: 100%;
    width: 100%;
    margin-left: 0;
    margin-top: 120px;
  }
}

.l-news {
  padding-bottom: 140px;
}
@media screen and (max-width: 767px) {
  .l-news {
    padding-bottom: 64px;
  }
}

.l-inner {
  max-width: 1200px;
  width: 84.375%;
  margin: 0 auto;
}
@media screen and (max-width: 1024px) {
  .l-inner {
    width: 100%;
    padding: 0 40px;
  }
}
@media screen and (max-width: 767px) {
  .l-inner {
    width: 100%;
    padding: 0 20px;
  }
}
.l-inner--narrow {
  max-width: 960px;
}
.l-inner--tiny {
  max-width: 800px;
}

.l-window-full {
  margin: 0 calc(50% - 50vw);
  overflow: hidden;
  width: 100vw;
}
.l-window-right {
  display: block;
  margin: 0 calc(50% - 50vw);
  overflow: hidden;
  padding: 0 0 0 calc(50vw - 50%);
  width: 100vw;
}
.l-window-left {
  display: block;
  margin: 0 calc(50% - 50vw);
  overflow: hidden;
  padding: 0 calc(50vw - 50%) 0 0;
  width: 100vw;
}

.l-footer {
  width: 100%;
  background: #152e30;
  color: #fff;
}

.c-accordion {
  position: relative;
}
.c-accordion.is-active .c-accordion__btn::after {
  transform: translateY(-50%) rotate(180deg);
}
.c-accordion__btn::before, .c-accordion__btn::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 32px;
  transform: translateY(-50%);
  width: 24px;
  height: 4px;
  background-color: #fff;
  border-radius: 50px;
  transition: 0.3s;
}
@media screen and (max-width: 767px) {
  .c-accordion__btn::before, .c-accordion__btn::after {
    width: 12px;
  }
}
.c-accordion__btn::after {
  transform: translateY(-50%) rotate(90deg);
}

.c-btn--left a,
.c-btn--left button {
  margin-right: auto;
}
.c-btn--right a,
.c-btn--right button {
  margin-left: auto;
}
.c-btn--center a,
.c-btn--center button {
  margin: 0 auto;
}

.c-border-btn a,
.c-border-btn button {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 320px;
  width: 100%;
  padding: 16px 0;
  height: 56px;
  border: 1px solid #ccc;
  color: #152e30;
  text-align: center;
  transition: 0.3s;
  border-radius: 50px;
}
@media screen and (max-width: 767px) {
  .c-border-btn a,
  .c-border-btn button {
    max-width: 100%;
    height: 56px;
  }
}
.c-border-btn--white a,
.c-border-btn--white button {
  border: 2px solid #fff;
  color: #fff;
}

.c-btn a,
.c-btn button {
  background: transparent;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  max-width: 264px;
  width: 100%;
  height: 56px;
  padding: 8px 16px;
  color: #152e30;
  transition: 0.3s;
  font-weight: 700;
  z-index: 0;
  background-color: #fff;
  border-radius: 50px;
}
@media screen and (max-width: 767px) {
  .c-btn a,
  .c-btn button {
    max-width: 100%;
    height: 56px;
  }
}
@media screen and (min-width: 1025px) {
  .c-btn a:hover,
  .c-btn button:hover {
    opacity: 0.7;
  }
}
.c-btn--kv a {
  max-width: 264px;
  width: 100%;
  height: 56px;
}
@media screen and (max-width: 767px) {
  .c-btn--kv a {
    height: 48px;
  }
}
.c-btn--cv a {
  position: relative;
  background-image: linear-gradient(to right, #19c2a4 0%, #53eeae 100%);
  color: #fff;
}
@media screen and (min-width: 1025px) {
  .c-btn--cv a:hover::before {
    opacity: 0.7;
  }
}

.c-btn-modal-close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: calc(100% + 60px);
  margin-bottom: -30px;
  margin-left: -30px;
  height: 56px;
  background-color: #152e30;
  color: #fff;
  font-size: 15px; /*IE9以下で反映されるよう記述*/
  font-size: 1.5rem;
  line-height: 1;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .c-btn-modal-close {
    border-radius: 0 0 12px 12px;
  }
}

.c-text-btn {
  position: relative;
  padding-left: 28px;
  color: #152e30;
  font-size: 14px; /*IE9以下で反映されるよう記述*/
  font-size: 1.4rem;
  letter-spacing: 0.06em;
  line-height: 1.7;
  transition: opacity 0.3s;
}
.c-text-btn::before {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 24px;
  height: 12px;
  transform: translateY(-50%);
  background-image: url(../img/icon/arrow_black_short.svg);
  background-position: left center;
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
}
@media screen and (max-width: 767px) {
  .c-text-btn::before {
    width: 41px;
    height: 11px;
  }
}

.swiper {
  position: relative;
}

.swiper-button-prev,
.swiper-button-next {
  width: 48px;
  height: 48px;
  box-shadow: 4px 4px 20px 0px rgba(0, 0, 0, 0.15);
  border-radius: 50%;
  background-color: #fff;
}
@media screen and (max-width: 767px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 36px;
    height: 36px;
  }
}
.swiper-button-prev::after,
.swiper-button-next::after {
  color: #152e30;
  content: "";
  background-image: url(../img/icon_caret.svg);
  width: 32px;
  height: 32px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.swiper-button-prev::after {
  transform: scale(-1, 1);
}

.swiper-button-prev {
  left: -18px;
}

.swiper-button-next {
  right: -18px;
}

.swiper-button-disabled {
  background-color: #f8f8f8;
  box-shadow: none;
  pointer-events: none;
}
.swiper-button-disabled::before {
  filter: grayscale(100%);
}

.swiper-pagination {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: center;
  margin-top: 20px;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .swiper-pagination {
    gap: 4px;
  }
}

.swiper-pagination-bullet {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  cursor: pointer;
  transition: 0.3s ease;
  vertical-align: top;
  background-color: #828282;
}

.swiper-pagination-bullet-active {
  background: #152e30;
}

.swiper-scrollbar {
  position: relative;
  margin-top: 5px;
  border-radius: 12px;
}
.swiper-scrollbar::after {
  position: absolute;
  z-index: -1;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 5px;
  margin: auto;
  content: "";
  background-color: #ddd;
  border-radius: 50px;
}

.swiper-scrollbar-drag {
  height: 5px;
  cursor: grab;
  border-radius: 50px;
  background-color: #828282;
  background-clip: content-box;
}

.swiper-slide-thumb-active::before {
  background: transparent !important;
}

.swiper-fade .swiper-slide {
  transition-property: opacity !important;
}

